import React from 'react';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export function MobileUndoButton(props: MobileToolbarButtonProps) {
  const { controller, Wrapper, item } = props;
  const isDisabled = !controller.query('canUndo');
  const handleClick = React.useCallback(
    () => controller.dispatch('undo'),
    [controller],
  );

  return (
    <Wrapper
      item={item}
      type="big"
      icon="undo"
      isDisabled={isDisabled}
      onClick={handleClick}
    />
  );
}
